// app/page.tsx  (الصفحة الرئيسية في App Router)
import Header from "../components/Header";
import Footer from "../components/Footer";
import HeroSlider from "../components/HeroSlider";
import MovieCard from "@/components/MovieCard";

import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "swiper/css/autoplay";

import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Autoplay } from "swiper/modules";

// جلب البيانات من السيرفر مباشرة
async function fetchContents() {
  const res = await fetch("http://localhost:8000/api/contents"); // رابط API الخاص بك
  const data = await res.json();
  return Array.isArray(data.data) ? data.data : [];
}

export default async function Home() {
  const contents = await fetchContents();

  return (
    <div className="min-h-screen bg-black text-white">
      <Header />

      {/* سلايدر المسلسلات */}
      <HeroSlider
        movies={contents
          .filter((c) => c.type === "series")
          .map((c) => ({
            id: c.uuid,
            title: c.title,
            description: c.description ?? "",
            image: `http://localhost:8000${c.poster_image ?? c.card_image}`,
            rating: c.rating ?? 0,
            year: c.year ?? "",
            genre: c.categories?.map((cat: any) => cat.name).join(", ") ?? "",
          }))}
      />

      <div className="px-4 md:px-8 lg:px-16 py-12 space-y-16">
        {renderSection("المسلسلات", "/series", contents, "series")}
        {renderSection("الأفلام", "/movies", contents, "movie")}
        {renderSection("البرامج", "/programs", contents, "program")}
        {renderSection("البودكاست", "/podcasts", contents, "podcast")}
        {renderSection("المسرحيات", "/plays", contents, "play")}
        {renderSection("الأطفال", "/kids", contents, "kids")}
      </div>

      <Footer />
    </div>
  );
}

function renderSection(
  title: string,
  link: string,
  allData: any[],
  type: string
) {
  const data = allData.filter((item) => item.type === type);
  if (data.length === 0) return null;

  return (
    <section>
      <div className="flex items-center mb-8 justify-between">
        <h2 className="text-2xl md:text-3xl font-bold text-white">{title}</h2>
        <a
          href={link}
          className="bg-red-600 hover:bg-red-700 text-white px-3 py-1 rounded text-s font-medium transition-colors whitespace-nowrap cursor-pointer flex items-center space-x-1"
        >
          عرض الكل
        </a>
      </div>

      <Swiper
        modules={[Navigation, Autoplay]}
        spaceBetween={16}
        loop={true}
        autoplay={{
          delay: 2000,
          disableOnInteraction: false,
          pauseOnMouseEnter: true,
        }}
        grabCursor={true}
        slidesPerView={2}
        breakpoints={{
          640: { slidesPerView: 2 },
          768: { slidesPerView: 3 },
          1024: { slidesPerView: 6 },
        }}
        className="w-full"
      >
        {data.map((item) => (
          <SwiperSlide key={item.uuid}>
            <MovieCard
              movie={{
                id: item.uuid,
                title: item.title,
                cardimg: `http://localhost:8000${item.card_image}`,
                image: `http://localhost:8000${item.poster_image}`,
                rating: item.rating ?? 0,
                year: item.year ?? 0,
              }}
              type={type}
            />
          </SwiperSlide>
        ))}
      </Swiper>
    </section>
  );
}
